<template>
	<view class="wrapper">
		<view class="user-info-wrap" v-if="userInfo">
			<view class="avatar-wrap"><image class="avatar" :src="userInfo.headImgUrl"></image></view>
			<view class="user-info">
				<text class="name">{{ userInfo.nickname }}</text>
				<navigator class="record">购买记录<image class="icon-arrows" src="../../static/images/icon-arrows.png"></image></navigator>
			</view>
		</view>
		
		<!-- 盲盒列表 -->
		<view class="blindbox-show-wrap">
			<view class="title-wrap">
				<view class="title">
					<image class="icon-choose" src="../../static/images/icon-choose.png"></image>精选盲盒
				</view>
				<view class="news-wrap">
					<image class="icon-news" src="../../static/images/icon-news.png"></image>
					<notice-bar ref='notice' color="#000000" bgColor="#FFF8AB" :direction="'level'" :scrollable="true" :text="textes" :speed=".12"/>
					<!-- <view class="text">恭喜 <text class="user-name">小小笑笑</text> 喜提盲盒！</view> -->
					</view>
			</view>
			<view class="blindbox-list">
				<!-- <view class="list-item">
					<view class="item-wrap">						
						<navigator class="link">
							<image class="cover" src="https://z3.ax1x.com/2021/07/05/RhUvut.png"></image>
							<view class="name">TOM and JERRY猫和老
	鼠系列TOM and JERRY猫和老鼠系列TOM and JERRY猫和老鼠系列</view>
						</navigator>
					</view>
					<view class="btn-pay">88元/次</view>
				</view> -->
				<block v-for="(item,index) of prizePoolList" :key="index">
					<view class="list-item" >
						<view class="item-wrap">						
							<navigator class="link" :url="`/pages/prize/prize?prizePoolId=`+item.prizePoolId">
								<image class="cover" :src="baseUrl+item.imgUrl"></image>
								<view class="name">{{item.title}}</view>
							</navigator>
						</view>
						<view class="btn-pay" @tap="showModal" data-target="Modal" :data-prizePoolId="item.prizePoolId">{{item.price}}元/次</view>
					</view>
				</block>
				
			</view>
		</view>
		<!-- 盲盒列表end -->
		
		
		<view class="cu-bar bg-white margin-top">
			<view class="action">
				<text class="cuIcon-title text-orange "></text> 普通窗口
			</view>
			<view class="action">
				<button class="cu-btn bg-green shadow" @tap="showModal" data-target="Modal">Modal</button>
			</view>
		</view>
		
		<!-- 活动规则 -->
		<view class="cu-modal rule-modal" :class="modalName=='Modal'?'show':''">
			<view class="cu-dialog">
				<view class="cu-bar bg-white justify-end modal-head">
					<view class="content">活动规则</view>
				</view>
				<view class="modal-content">
					1、本活动使用微信登录和微信支付。<br/>
					2、选择心仪的盲盒系列，每次需要支付金额购买。<br/>
					3、幸运盲盒将根据用户抽中的系列随机发放，以用户收到的实物为准。<br/>
					4、抽中盲盒后，请及时填写收件人相关信息，从抽中之时起超过48小时未填写，将视为自动放弃。<br/>
					5、本次活动的最终解释权归活动举办方所有。
				</view>
				<view class="btn-read" @tap="hideModal" :data-prizepoolid="prizepoolid">我已阅读并了解</view>
			</view>
		</view>
		<!-- 活动规则end -->
		
	</view>
</template>

<script>
	
	import noticeBar from '../../components/xyh-notice-bar/xyh-notice-bar.vue'
	export default {
		components:{noticeBar},
		data() {
			return {
				modalName: null,
				userInfo: null,
				//中奖展示信息
				textes:['李先生中奖了','张先生中奖了'],
				
				//盲盒列表
				prizePoolList: [],
				baseUrl: this.$url.baseUrl,
				
				//奖品ID
				prizepoolid: null,
				
			}
		},
		onLoad(e) {
			
			let userInfo = uni.getStorageSync('userInfo');
			
			if(userInfo == null || userInfo == "") {
				//如何为空获取code发起请求
				let code = this.getUrlCode('code')
				console.log(code)
				if(code !== null || code !== "") {
				    this.getOpenidAndUserinfo(code)
				}
				
			} else {
				
				//有数据直接赋值
				this.userInfo = userInfo;
				
				
			}
			
			this.getPrizePoolListUrl();
			
		},
		mounted() {
		    this.$refs.notice.init(this.textes)
		},
		methods: {
			showModal(e) {
				this.modalName = e.currentTarget.dataset.target
				
				//
				console.log(e);
				console.log(e.currentTarget.dataset.prizepoolid);
				
				
			},
			hideModal(e) {
				this.modalName = null
			},
			getUrlCode (name) {
				return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ''])[1].replace(/\+/g, '%20')) || null
			},
			async getOpenidAndUserinfo(code) {
				// console.log(code);
				let res = await this.$request({
					url: this.$url.wxloginUrl,
					method: 'post',
					data: {
						code: code,
						// state: 'state',
						// appid: 'wx1c9d77f2a708e46d'
					}
				});
				
				console.log(res);
				// console.log(res.data.data.userInfo);
				
				if(res.data.code == 0) {
					
					// this.userInfo = res.data.data.userInfo;
					
					uni.setStorageSync('userInfo', res.data.data.userInfo);
					uni.setStorageSync('token', res.data.data.token);
					
					this.userInfo = uni.getStorageSync('userInfo');
				}
				
			},
			//获取抽奖列表
			async getPrizePoolListUrl() {
				
				let res = await this.$request({
					url: this.$url.prizePoolListUrl,
				});
				
				this.prizePoolList = res.data.data;
				
				console.log(res);
			}
		}
	}
</script>

<style lang="scss">
	.wrapper {
		background: #ffffff;
	} 
	//用户信息 
	.user-info-wrap {
		display: flex;
		width: 750rpx;
		height: 180rpx;
		padding: 30rpx;
		background: #FD5C40;
		border-radius: 0 0 0 90rpx;
		box-sizing: border-box;
		.avatar-wrap {
			display: inline-block;
			width: 120rpx;
			height: 120rpx;
			margin-right: 20rpx;
			.avatar {
				width: 100%;
				height: 100%;
				background: #D2D2D2;
				border-radius: 50%;
			}
		}
		.user-info {
			.name {
				line-height: 70rpx;
				font-size: 34rpx;
				font-weight: bold;
				color: #FFF4DA;
			}
			.record {
				font-size: 24rpx;
				font-weight: 400;
				color: #FFFEFE;
				.icon-arrows {
					display: inline-block;
					width: 10rpx;
					height: 17rpx;
					margin-left: 20rpx;
				}
			}
		}
	}
	
	//盲盒列表
	.blindbox-show-wrap {
		padding: 20rpx 15rpx;
		.title-wrap {
			display: flex;
			justify-content: space-between;
			padding: 0 15rpx;
			.title {
				font-size: 30rpx;
				font-weight: 400;
				color: #000000;
				.icon-choose {
					display: inline-block;
					width: 32rpx;
					height: 38rpx;
					margin-right: 8rpx;
					vertical-align: sub;
				}
			}
			.news-wrap {
				display: flex;
				width: 370rpx;
				height: 50rpx;
				line-height: 50rpx;
				background: #FFF8AB;
				border-radius: 25rpx;
				font-size: 24rpx;
				vertical-align: top;
				.icon-news {
					display: inline-block;
					width: 29rpx;
					height: 21rpx;
					margin: 15rpx;
				}
				.text {
					
					.user-name {
						color: #FD5C40;
					}
				}
			}
		}
		.blindbox-list {
			.list-item {
				display: inline-block;
				width: 360rpx;
				padding: 20rpx 15rpx;
				box-sizing: border-box;
				.item-wrap {
					width: 100%;
					height: 460rpx;
					background: #F0F0F0;
					border-radius: 30rpx;
					.link {
						width: 100%;
						.cover {
							width: 100%;
							display: block;
							height: 330rpx;
							border-radius: 30rpx 30rpx 0 0;
						}
						.name {
							display: -webkit-box;
							// height: 130rpx;
							font-size: 28rpx;
							font-weight: 400;
							margin: 20rpx;
							box-sizing: border-box;
							-webkit-box-orient: vertical;
							-webkit-line-clamp: 2;
							overflow: hidden;
						}
					}
				}
				.btn-pay {
					margin: -25rpx auto 0 auto;
					width: 170rpx;
					height: 50rpx;
					line-height: 50rpx;
					font-size: 28rpx;
					font-weight: bold;
					color: #FFFFFF;
					background: #FD5C40;
					border-radius: 25px;
					text-align: center;
				}
			}
		}
	}

	// 活动规则
	.rule-modal {
		.cu-dialog{
			width: 600rpx;
			border-radius: 30rpx;
		}
		.modal-head {
			height: 80rpx;
			font-size: 34px;
			font-weight: bold;
			color: #FFFFFF;
			background: #FD5C40;
			border-radius: 30rpx 30rpx 0 0;
		}
		.modal-content {
			padding: 28rpx 33rpx;
			font-size: 28rpx;
			font-weight: 400;
			color: #000000;
			line-height: 40rpx;
			text-align: left;
		}
		.btn-read {
			width: 300rpx;
			height: 70rpx;
			line-height: 70rpx;
			margin: 10rpx auto 30rpx auto;
			font-size: 30rpx;
			font-weight: 400;
			color: #FFFFFF;
			background: #FD5C40;
			border-radius: 35rpx;
		}
	}
</style>
